<script>
  import TrackerButton from "./tracker-button.svelte";
  import { activeboard, activelog } from "../../store";
  import datastore from "../../modules/datastore/datastore";

  // Let active startoff as null
  let board = null;
  let nomieLog = null;

  // Subscribe to the Active Board
  activeboard.subscribe(value => {
    board = value;
  });
  activelog.subscribe(value => {
    nomieLog = value;
  });
  let trackerTap = tracker => {
    console.log("Tracker Tapps", tracker);
    nomieLog.addTag(tracker.tag);
    activelog.update(n => {
      return nomieLog;
    });
  };
</script>

<style type="text/scss" name="scss">
  .n-board {
    display: flex;
    flex-direction: column;
    align-items: space-between;
    justify-content: center;
    padding: 20px;
    background-color: #f9f9f9;
    position: fixed;
    top: 0;
    bottom: 50px;
    left: 0;
    right: 0;
    padding-bottom: 100px;
    overflow-y: scroll;
  }
  .n-board .trackers {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: auto auto auto auto;
  }
</style>

<main class="n-board">

  {#if board}
    <div class="trackers">
      {#each board.trackers as tracker (tracker.id)}
        <TrackerButton
          label={tracker.label}
          emoji={tracker.emoji}
          on:click={() => {
            trackerTap(tracker);
          }} />
      {/each}
    </div>
  {:else}
    <div class="no-board">No board selected</div>
  {/if}
</main>
